.container {
  position: relative;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.inputForm {
  position: relative;
  display: flex;
  align-items: center;
  background-color: var(--color-bg);
  margin: 0.4rem 0.6rem;
  border-radius: 10px;
  border: 1px solid var(--color-main);
  box-shadow: var(--shadow);
  -webkit-box-shadow: var(--shadow);
  -moz-box-shadow: var(--shadow);
}

.inputText {
  width: 100%;
  border: none;
  font-size: var(--font-size-medium);
  outline: none;
}

.items {
  background-color: var(--color-bg);
  height: 100%;
  overflow-y: auto;
  margin: 0 var(--maring-side);
  border-radius: 10px;
  margin: 0 var(--margin-side);
  border: 1px solid var(--color-main);
  box-shadow: var(--shadow);

  -webkit-box-shadow: var(--shadow);
  -moz-box-shadow: var(--shadow);
}
