.note {
  padding: var(--padding-side);
  border-bottom: 1px solid var(--color-main);
  color: var(--color-main);
}

.header {
  margin-bottom: 0.4rem;
}
.sender {
  font-weight: bold;
}

.reciver {
  color: #42a5f5;
}

.message {
  word-break: break-all;
}

.time {
  margin-left: 0.4rem;
  opacity: 0.5;
}
